* {
    box-sizing: border-box;
}

.login {
    width: 100%;
    height: 60vw;
    text-align: center;
    position: relative;
    background-image: url('../../../public/img/short_bg.png');
    background-size: cover;
    background-repeat: no-repeat;
    background-position: top center;
}

.login h1 {
    color: #ffffffee;
    font-weight: bold;
    font-size: 2vw;
    padding-top: 10vw;
    padding-bottom: 3vw;
}

.login input {
    width: 20vw;
    height: 3vw;
    margin-bottom: 1vw;
}

.login input::placeholder {
    font-size: 1vw;
}

.login button {
    width: 8vw;
    height: 3vw;
    border: 1px solid #ffffffa1;
    background: none;
    color: #ffffffc0;
    font-size: 1vw;
    font-weight: bold;
    margin-top: 4vw;
}

.login button:hover {
    color: #ffffffee;
    border: 1px solid #ffffffc0;
}

.login div p {
    display: inline-block;
    margin: 0 auto;
    width: 20vw;
    text-align: right;
    font-size: 1vw;
    color: #ffffffa1;
    text-decoration: underline;
    cursor: pointer;
}

.login>p {
    display: inline-block;
    font-size: 1vw;
    margin-top: 2vw;
    color: #ffffffa1;
    text-decoration: underline;
    cursor: pointer;
}

.login p:hover {
    color: #ffffffee;
}

@media (max-width: 767px) {
    .login {
        height: 100vw;
    }

    .login h1 {
        font-size: 4vw;
        padding-top: 10vw;
        padding-bottom: 3vw;
    }

    .login input {
        width: 50vw;
        height: 10vw;
        margin-bottom: 3vw;
    }

    .login input::placeholder {
        font-size: 3vw;
    }

    .login button {
        width: 16vw;
        height: 6vw;
        font-size: 3vw;
        margin-top: 8vw;
    }

    .login div p {
        display: block;
        margin: 0 auto;
        width: 50vw;
        text-align: right;
        font-size: 2vw;
        color: #ffffffa1;
    }

    .login>p {
        display: block;
        font-size: 2vw;
        padding-top: 4vw;
        color: #ffffffa1;
    }
}