.cart {
    width: 100%;
    text-align: center;
    position: relative;
    background-image: url('../../../public/img/bg.png');
    background-size: cover;
    background-repeat: no-repeat;
    background-position: top center;
    padding-bottom: 10vw;
    font-size: 1vw;
}

.cart h1 {
    color: #ffffffee;
    font-weight: bold;
    font-size: 2vw;
    padding-top: 10vw;
    padding-bottom: 8vw;
}

.cart .table {
    margin-bottom: 0;
}

.cart tr {
    color: #ffffffee;
}

.cart td {
    vertical-align: middle;
    line-height: 100%;
}

.cart .img {
    width: 28vw;
}

.cart img {
    border: 1px solid pink;
    cursor: pointer;
}

.empty {
    font-size: 5vw;
    color: #ffffffee;
}

.cart button {
    color: #ffffffee;
    border: 0;
    background: none;
    font-size: 2vw;
}

.total td {
    padding: 5vw 0 1vw 0;
}

.order {
    margin: 0 auto;
    color: #ffffffee;
    font-size: 4vw;
    padding: 1vw 0;
}

.order:hover {
    font-weight: bold;
}

.fee {
    font-size: 0.8vw;
}

@media (max-width: 767px) {
    .cart {
        font-size: 2vw;
    }

    .cart h1 {
        font-size: 4vw;
    }

    .cart button {
        font-size: 4vw;
    }
}

