a {
    text-decoration: none;
}

.home {
    background-color: black;
}

.slider {
    width: 100%;
    height: 60vw;
    text-align: center;
    position: relative;
    background-image: url('../../../public/img/short_bg.png');
    background-size: cover;
    background-repeat: no-repeat;
    background-position: top center;
}

.slider h1 {
    color: #ffffffee;
    font-weight: bold;
    font-size: 2vw;
    position: absolute;
    top: 3.15vw;
    /* 60px */
    left: 50%;
    transform: translate(-50%, 0);
}

.sliderSwiper {
    width: 69.4vw;
    /* 1320px */
    height: 29.43vw;
    /* 560px */
    position: absolute;
    top: 8.41vw;
    /* 160px */
}

.sliderSwiper img {
    border-radius: 0.53vw;
    /* 10px */
    width: 70%;
}

.swiper-button-prev,
.swiper-button-next {
    color: #ffffffc0 !important;
    margin-top: -3.15vw !important;
    /* -60px */
}

.swiper-pagination-bullet {
    background-color: #ffffffc0 !important;
    margin: 0 0.42vw !important;
    /* 8px */
}

.swiper-pagination-bullet-active {
    background-color: #ffffffa1 !important;
}

.swiper-pagination {
    position: absolute;
    bottom: 0;
}

.me-auto {
    position: relative;
    z-index: 10;
}

.bn {
    width: 69.4vw;
    margin: 0 auto;
    position: absolute;
    top: 42.03vw;
    left: 50%;
    transform: translate(-50%, 0);
    display: flex;
    justify-content: space-between;
}

.bn>* {
    padding: 0;
}

.bn img {
    width: 21.01vw;
}

.notice {
    width: 83.33vw;
    height: 20.52vw;
    margin: 0 auto;
    display: flex;
    align-items: center;
}

.notice h3 {
    width: 20.83vw;
    height: 20.52vw;
    line-height: 20.52vw;
    text-align: center;
    font-size: 1.56vw;
    color: #ffffffee;
}

.notice table {
    width: 62.5vw;
    height: 15.63vw;
    font-size: 1.25vw;
    color: #ffffffc0;
}

.notice table tr:hover {
    cursor: pointer;
}

.items {
    text-align: center;
}

.items h1 {
    color: #ffffffee;
    font-weight: bold;
    font-size: 2vw;
    padding-top: 10vw;
}

.items .row {
    margin-top: 8vw;
}

.items .row img {
    border: 1px solid pink;
    cursor: pointer;
}

.items .row h4 {
    width: 80%;
    margin-left: 10%;
    margin-top: 5%;
    color: #ffffffee;
    font-size: 1vw;
    text-align: justify;
}

.items .row p {
    width: 80%;
    margin-left: 10%;
    color: #ffffffc0;
    font-size: 0.8vw;
    text-align: start;
}

.items .row p span {
    font-size: 0.8vw;
}

.items button {
    border-top: 1px solid #ffffffa1;
    border-bottom: 1px solid #ffffffa1;
    border-left: 0;
    border-right: 0;
    background-color: black;
    color: #ffffffc0;
    font-size: 1.5vw;
    letter-spacing: 0.2vw;
    font-weight: bold;
}

.birthday {
    padding-bottom: 10vw;
}

.items .row>* {
    margin-bottom: 5vw;
}

@media (max-width: 767px) {
    .bn img {
        width: 80vw;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
    }

    .bn>* {
        margin-bottom: 3vw;
        position: relative;
    }

    .swiper-pagination-bullet {
        width: 0.5vw;
        height: 0.5vw;
    }

    .sliderSwiper {
        width: 85vw;
        height: 38vw;
        top: 12vw;
    }

    .swiper-button-prev,
    .swiper-button-next {
        color: #ffffffc0 !important;
        margin-top: -6vw !important;
    }

    .slider h1 {
        font-size: 4vw;
    }

    .bn {
        width: 69.4vw;
        height: 120vw;
        margin: 0 auto;
        position: absolute;
        top: 60vw;
    }

    .slider {
        height: 200vw;
    }

    .notice {
        width: 100%;
        height: auto;
        display: block;
        padding-bottom: 5vw;
    }

    .notice h3 {
        width: 100%;
        height: 18vw;
        line-height: 22vw;
        text-align: left;
        padding-left: 10vw;
        font-size: 5vw;
        color: #ffffffee;
    }

    .notice table {
        width: 80vw;
        margin: 0 auto;
        height: 15.63vw;
        font-size: 2vw;
        color: #ffffffc0;
    }

    .items h1 {
        font-size: 5vw;
        padding-top: 25vw;
    }

    .items .row {
        margin-top: 15vw;
    }

    .items .row h4 {
        font-size: 3vw;
    }

    .items .row p {
        font-size: 2.4vw;
    }

    .items .row p span {
        font-size: 2vw;
    }

    .items button {
        font-size: 3vw;
    }

    .birthday {
        padding-bottom: 20vw;
    }
}