.detail {
    width: 100%;
    position: relative;
    background-image: url('../../../public/img/short_bg.png');
    background-size: cover;
    background-repeat: no-repeat;
    background-position: top center;
}

.detail .container {
    padding-top: 5vw;
}

.detail .row {
    margin-bottom: 5vw;
}

.detail .row img {
    border: 1px solid pink;
}

.detail .row h4 {
    text-align: left;
    font-size: 1.5vw;
    color: #ffffffee;
}

.detail .row p {
    color: #ffffffc0;
    font-size: 1vw;
    margin-top: 1vw;
}

.detail .row p span {
    font-size: 0.8vw;
}

.btns {
    position: absolute;
    bottom: 0;
    padding: 0;
}

.detail .row button {
    color: black;
    background-color: #ffffffee;
    font-size: 1vw;
    height: 3vw;
    border: 0;
    border-radius: 0.1vw;
}

.detail .row button:nth-of-type(1) {
    width: 10vw;
}

.detail .row button:nth-of-type(2) {
    width: 3vw;
    margin-left: 0.5vw;
}

.detail .row button:hover {
    opacity: 0.8;
}

.tab .nav-link {
    color: #ffffffee;
}

@media (max-width: 767px) {
    .col-md-6:nth-of-type(2) {
        height: 40vw;
        padding-top: 8vw;
    }

    .detail .row h4 {
        text-align: center;
        font-size: 3vw;
        text-align: justify;
    }

    .detail .row p {
        font-size: 3vw;
        margin-top: 3vw;
    }

    .detail .row p span {
        font-size: 2vw;
    }

    .detail .row button {
        font-size: 2vw;
        height: 6vw;
    }

    .detail .row button:nth-of-type(1) {
        width: 20vw;
    }

    .detail .row button:nth-of-type(2) {
        width: 6vw;
        margin-left: 1vw;
    }
}