footer {
    width: 100%;
    height: auto;
    text-align: center;
    background-color: #ffffff;
    border-top: 1px solid pink;
}

footer img {
    width: 20vw;
    margin-top: 3vw;
    margin-bottom: 3vw;
}

footer .rights {
    width: 66.66vw;
    margin: 0 auto;
    color: gray;
    font-size: 0.78vw;
    padding: 5vw 0 2.6vw 0.52vw;
}

.rights p:nth-of-type(1) {
    padding-bottom: 0.8vw;
}

footer .rights>* {
    margin-bottom: 0;
    line-height: 1.3vw;
}

@media (max-width: 767px) {
    footer img {
        width: 50vw;
        margin-top: 5vw;
        margin-bottom: 10vw;
    }

    footer .rights {
        width: 100%;
        height: 15vw;
        color: gray;
        padding: 0;
    }

    footer .rights p {
        margin: 0 auto;
        color: gray;
        text-align: center;
        line-height: 3vw;
    }

    .rights p:nth-of-type(1) {
        font-size: 2.2vw;
        padding-bottom: 2vw;
    }

    footer .rights p:nth-of-type(2) {
        font-size: 1.8vw;
        width: 80vw;
    }
}